.main {
    margin-left: 0;
    /* Same as the width of the sidenav */
}


/* Product */

.cards {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}


/* Float four columns side by side */

.column {
    float: left;
    width: 25%;
    padding: 0 10px;
}


/* Remove extra left and right margins, due to padding */

.row {
    margin: 0 -5px;
}


/* Clear floats after the columns */

.row:after {
    content: "";
    display: table;
    clear: both;
}

.card {
    box-shadow: 5px 4px 8px 0 rgb(0 0 0 / 8%);
    margin: auto;
    text-align: center;
    font-family: 'Dosis', sans-serif;
    font-family: 'Dosis';
    font-weight: 600;
    border-bottom: 2px dashed #b4b1b161;
    flex: 0 0 33.33%;
    margin-left: 15%;
    padding: 0 0 7px 0;
}

.price {
    color: grey;
    font-size: 22px;
}

.card button {
    border: none;
    outline: 0;
    padding: 9px;
    color: #fff;
    background-color: #b1086d;
    text-align: center;
    cursor: pointer;
    width: 100%;
    font-size: 13px;
    margin-top: 10px;
}

.description {
    background-color: #b4b1b173;
    text-align: center;
}


